html {
    height: 100%;
  }

body {
  position: relative;
  margin: 0;
  min-height: 100%;
  height: 100%;
  font-family: Slack-Larsseit,"Helvetica Neue",Helvetica,"Segoe UI",Tahoma,Arial,sans-serif;
  background-color: #263238;
}

#footer {
    position: absolute;
    bottom: 0;
    padding: 1rem;
    text-align: center;
    border-top: 1px solid gray;
}

#footer-item-container {
    margin: auto;
    padding: 5px;
    width: 300px;
    height: 30px;
}

.footer-item {
    margin: 20px;
}

.footer-image:hover {
    opacity: 0.8;
    cursor: pointer;
}

#root {
    height: 100%;
}
#menubar-container {
    width: 100%;
    padding: 20px 0px 20px 0px;
    text-align: right;
    transition: 0.5s;
    z-index: 2;
    background-color: #263238;
}

#menubar-item-list {
    display: flex;
}

.menubar-item {
    font-size: 20px;
    padding: 10px;
    margin: 50px;
    color: #E0E0E0;
    font-family: Varela Round, sans-serif;
    text-decoration: none;
    opacity: 0.7;
    transition: 0.3s;
    border-bottom: 1px solid transparent;
}

.menubar-item:hover {
    opacity: 1;
    border-bottom: 1px solid #E0E0E0;
}

#mobile-menu-button {
    background-color: transparent;
    border: 1px solid black;
    border-radius: 4px;
    transition: 0.3s;
    margin: 10px;
}

#mobile-menu-button:hover {
    background-color: lightgray;
    transition: 0.3s;
    cursor: pointer;
}

#mobile-menu-button:focus {
    outline: none;
}

#mobile-menubar-container {
    width: 100%;
    display: block;
    text-align: right;
    background-color: #263238;
}

#mobile-menubar-item-container {
    display: inline-block;
    transition: 0.3s;
    width: 100%;
    background-color: lightgray;
}

#mobile-menubar-item-list {
    list-style: none; 
    padding-left: 0px;
    text-align: left; 
    margin-top: 25px;
}

#mobile-menubar-item {
    font-size: 20px;
    padding: 5px;
    color: black;
    font-family: Varela Round, sans-serif;
    text-decoration: none;
    transition: 0.4s;
    opacity: 0.9;
    border-radius: 5px;
    width: 100%;
}

#mobile-menubar-item:hover {
    opacity: 1;
}

#mobile-menubar-list-item {
    padding: 20px;
    transition: 0.3s;
}

#mobile-menubar-list-item:hover {
    background-color: gray;
    transition: 0.3s;
    cursor: pointer;
}

.mobile-menu-line {
    width: 35px;
    height: 3px;
    background-color: black;
    margin: 6px 0;
    border-radius: 20px;
}
#home-body-container {
    height: 100%;
    width: 100%;
    background-color: #263238;
    text-align: center;
}

#home-body-container div {
    margin-top: 150px;
    padding: 50px;
}

#home-body-container h1 {
    font-size: 80px;
    margin-bottom: 0px;
    margin-top: 0px;
    color: #E0E0E0;
}

#home-body-container h3 {
    font-size: 40px;
    margin-top: 0px;
    color: #E0E0E0;
}

#home-body-container p {
    font-size: 20px;
    color: #E0E0E0;
    margin-bottom: 0px;
    max-width: 50%;
    margin: auto;
}

#home-top-image {
    height: 650px;
    width: 500px;
    min-width: 300px;
    min-height: 450px;
}

#top-content {
    font-size: 14px;
}

#icon-list {
    list-style: none; 
    display: flex; 
    margin: auto; 
    padding: 5px;
}

#home-top-title-container {
    margin: 200px;
}

#home-top-title {
    text-align: left;
}

#home-middle-section {
    font-family: Varela Round, sans-serif;
}

#home-middle-section img {
    width: 200px;
    height: 200px;
}

#middle-section-container {
    display: flex;
    padding: 20px 150px 20px 300px;
}

#about-image {
    max-width: 350px;
    margin: 20px;
}

#middle-content {
    max-width: 600px;
    margin: 120px;
}

#project-section {
    font-family: Varela Round, sans-serif;
    background-color: #263238;
    height: 1000px;
}

#skills-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    flex-grow: 1;
    justify-content: center;
}

#contact-section {
    background-color: #78909C;
    padding: 50px;
}

#contact-section a {
    color: black;
    padding: 10px;
    transition: 0.3s;
    text-decoration: none;
    font-size: 17px;
    border-radius: 5px;
    background-color: gray;
    box-shadow: 0px 4px 27px -10px rgba(0,0,0,0.4);
}

#contact-section a:hover {
    cursor: pointer;
    background-color: hsl(0, 0%, 62%);
}

#contact-section p:first-child {
    font-size: 40px;
    margin-bottom: 0px;
}

#contact-section p:first-child + p {
    font-size: 30px;
    margin-top: 10px;
}

@media screen and (max-width: 1190px) {
    #home-body-content {
        display: block;
        width: 100%
    }

    #home-top-title-container {
        margin: 10px;
    }

    #home-top-title {
        text-align: center;
    }

    #home-top-image {
        width: 80%;
    }

    #top-content {
        margin: auto;
    }

    #icon-list {
        width: 300px;
    }
}

@media screen and (max-width: 660px) {
    .project-items {
        height: 700px;
    }

    #project-section {
        height: 1500px;
    }

    #skills-container img {
        width: 200px;
    }

    #skills-container div {
        margin: 10px;
    }
}

@media screen and (max-width: 450px) {
    #home-body-container div {
        padding: 5px;
    }

    #home-body-container div h1 {
        font-size: 57px;
    }

    #home-body-container div h3 {
        font-size: 37px;
    }

    #home-body-container p {
        max-width: 100%;
        margin: 0px;
    }
}
#about-container {
    font-family: Varela Round, sans-serif;
    background-color: #263238;
    color: white;
    padding: 50px;
}

#about-header {
    text-align: center;
    margin-top: 0px;
    font-size: 50px;
}

#about-text {
    font-size: 25px;
    text-align: left;
    text-shadow: 1px 0px 0px rgba(150, 150, 150, 1);
    height: 100%;
}

#about-text ul {
    padding: 20px;
    list-style: disc;
}

#about-text ul li {
    margin-left: 10px;
}

@media screen and (max-width: 410px) {
    #about-text {
        font-size: 20px; 
    }
}
#project-container {
    font-family: Varela Round, sans-serif;
    background-color: #263238;
    color: white;
    padding: 50px;
}

#project-container h1 {
    margin-top: 0px;
    font-size: 50px;
}

#project-item-container {
    padding: 10px;
    margin: 10px;
}

#project-container a {
    color: lightblue;
    text-decoration: none;
}

#project-container a:focus {
    color: lightblue;
    text-decoration: none;
}

.project-items {
    min-height: 500px;
}

@media screen and (max-width: 1160px) {
    .items {
        display: block;
        width: 500px;
    }
}

@media screen and (max-width: 600px) {
    .items {
        display: block;
        width: 300px;
    }
}

#skills-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 50px;
}

#skills-container p {
    color: white;
}

#skills-container img {
    max-width: 200px;
    max-height: 200px;
}

#skills-container ul {
    list-style: none;
    color: white;
    padding-left: 5px;
}

#skills-container ul li {
    padding: 2px;
}
footer {
    bottom: 0;
    position: absolute;
    width: 100%; 
    border-top: 1px solid lightgray; 
    height: 100px; 
    background-color: #E2E2E2;
}

footer div {
    font-size: 13px;
    margin: auto;
}

footer a {
    text-decoration: none;
    opacity: 0.8;
}

footer a:hover {
    opacity: 1;
}

footer img {
    width: 30px;
    height: 30px;
}

footer ul {
    width: 200px;
    margin: auto;
}

footer li {
    margin: 15px;
}

#footer-item-container {
    margin: auto;
    padding: 5px;
    width: 300px;
    height: 30px;
}

#footer-item {
    margin: 20px;
}

#footer-image:hover {
    opacity: 0.8;
    cursor: pointer;
}

@media screen and (max-width: 850px) {
    footer {
        position: relative;
    }
}
body {
    text-align: center;
}
